<template>
    <div class="main-content-wrap main-content-bg clearfix" v-wechat-title="VueTitle">
        <!-- 彩种导航区域开始 -->
        <div class="sub-nav-wrap">
			<div class="w-1200 clearfix">
				<div class="sub-nav-con" v-if="articleList.length">
                    <router-link :to="{path:'/article/page',query:{type: item.type}}" :class="{'active' : type == item.type}" v-for="(item,index) in articleList" :key="index">{{ english[item.name] }}</router-link>
				</div>
			</div>
		</div>
		<!-- 彩种导航区域结束 -->
        
        <div class="w-1200 trend-main-wrap clearfix">
            <div v-if="loading" class="contentLoad">
                <Spin fix size="large"></Spin>
            </div>
            <template v-else>
                <template v-if="articleContentFlag && !article.content">
                    <div class="noContent"></div>
                </template>
                <template v-else>
                    <div>
                        <h2 class="h2-title">{{article.title}}</h2>
                        <div class="ContentText" v-html="article.content"></div>
                    </div>
                </template>
            </template>
        </div>

    </div>
</template>
<script>
import { jsonToString } from "@/common/js/jsonToString";
import { mapGetters, mapActions } from "vuex";
import english from "../../assets/json/English.json";

export default {
    data() {
        return {
            english: english,
            VueTitle: english.玩法介绍, //页面标题
            
            type: 1,
            articleType: 2,
            article: {},
            articleList: {},
            articleContentFlag: false,
            loading: true
        };
    },
    created() {
        //获取菜单数据
        this.type = this.$route.query.type;
        this.$http.post(this.urlRequest + "?m=api&c=app&a=listArticleColumn", {type: this.articleType}).then(res => {                
            if (res.data.status == 0) {
                if (res.data.data.length) {
                    this.articleList = res.data.data;
                    this._getTypeArticle();
                }
            } else {
                this.$Message.error(res.data.ret_msg);
            }
        });
        
    },
    mounted() {
      
    },
    watch: {
        '$route':function(to, form) {
            this.type = this.$route.query.type;
            this.$http.post(this.urlRequest + "?m=api&c=app&a=listArticleColumn", {type: this.articleType}).then(res => {                
                if (res.data.status == 0) {
                    if (res.data.data.length) {
                        this.articleList = res.data.data;
                        this._getTypeArticle();
                    }
                } else {
                    this.$Message.error(res.data.ret_msg);
                }
            });
        },
    },
    computed: {
    },
    methods: {
        // 获取文章内容
        _getTypeArticle() {
            this.article = {};
            this.articleContentFlag = false;
            this.loading = true;            
            this.$http.post(this.urlRequest + "?m=api&c=app&a=getTypeArticle", {type: this.type}).then(res => {
                this.loading = false;
                this.articleContentFlag = true;
                if (res.data.status == 0) {
                    if (res.data.data.content) {
                        this.article = res.data.data;
                    }
                } else {
                    this.$Message.error(res.data.ret_msg);
                }
            });
        },
    }
};
</script>
<style scoped>
.contentLoad .ivu-spin-fix {
    width: 100%;
    height: 500px;
    position: relative;
    background: none;
}
</style>


